.featured-posts {
  @include respond-to('medium-large') {
    &.-multiple {
      display: flex;
      justify-content: space-between;
    }
  }

  &.-search-heading {
    margin: unit(1) 0 unit(2) 0;
    max-width: width('max');

    @include respond-to('medium-large') {
      margin: unit(-2) unit(2) 0;
      padding-bottom: unit(2);
    }
  }
}

.featured-post {
  + .featured-post {
    margin-top: unit(1);

    @include respond-to('medium-large') {
      margin-top: 0;
    }
  }

  .-multiple & {
    @include respond-to('medium-large') {
      width: calc(50% - #{unit(0.5)});
    }
  }
}

.featured-post__wrapper {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  margin: 0;
  padding: 0;
}

.featured-post__item {
  align-items: flex-end;
  color: $color--white;
  display: flex;
  height: 100%;
  min-height: 45rem;
  position: relative;
  text-decoration: none;

  @include respond-to('large') {
    min-height: 32vw;
  }

  @include respond-to('x-large') {
    height: 45rem;
    min-height: 0;
  }

  &::before {
    @include pseudo;
    background: linear-gradient(
      to bottom,
      rgba($color--tuatara, 0) 30%,
      rgba($color--tuatara, 0.9) 80%
    );
    bottom: 0;
    left: 0;
    right: 0;
    top: 0;
    z-index: 0;

    @include respond-to('medium') {
      background: linear-gradient(
        to left,
        rgba($color--tuatara, 0) 30%,
        rgba($color--tuatara, 0.9) 80%
      );

      .-multiple & {
        background: linear-gradient(
          to left,
          rgba($color--tuatara, 0) 0%,
          rgba($color--tuatara, 0.9) 70%
        );
      }
    }
  }
}

a.featured-post__item {
  @include hover {
    .featured-post__caption__heading {
      text-decoration: underline;
    }
  }
}

.featured-post__caption__flag {
  @include featured-flag;

  @include respond-to('medium') {
    left: unit(2);

    .-multiple & {
      left: unit(1);
    }
  }
}

.featured-post__caption {
  @include null-margins;
  font-size: 1.3rem;
  padding: unit(5) unit(1) unit(1);
  position: relative;
  z-index: 1;

  @include respond-to('medium') {
    padding: unit(2);
    width: 60%;
  }

  @include respond-to('medium-large') {
    .-multiple & {
      padding: unit(5) unit(1) unit(1);
      width: 100%;
    }
  }

  @include respond-to('large') {
    width: 50%;

    .-multiple & {
      width: 80%;
    }
  }

  p {
    margin: unit(0.5) 0;
  }
}

.featured-post__caption__heading {
  font: 2.4rem/1.2 $font-family--serif;
  margin: 0 0 unit(0.5);
}

.featured-post__caption__more {
  @include arrow;
}
